@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primary-color:white;
  --secondary-color:rgb(181, 225, 245)
}
.App{
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  background:rgb(1, 32, 82);
  padding: 1rem;
  margin:auto;
  width: 100%;
  max-width: 500px;
  color: white;
  color: var(--primary-color);

  
}
h1{
  color: white;
  color: var(--primary-color);
  font-size: 30px;
}
p{
  padding:0rem 1rem;
  color:rgb(181, 225, 245);
  color:var(--secondary-color)
}
.label,.cashGiven{
  font-size: 23px;
  padding: 1rem;
}
#cashAmt,#billAmt{
  display: block;
  margin:auto;
  margin-top: .9rem;
  padding: 1rem;
  width: 80%;
 
  border-radius: 4rem;
  border: none;
  
}
#next,#submit{
  padding: .5rem 2rem;
  font-size: 15px;
  margin-top: .7rem;
  background-color: rgb(181, 225, 245);
  background-color: var(--secondary-color);
  border: none;
  font-family: 'Montserrat', sans-serif;
  border-radius: .3rem;
  cursor: pointer;

  

}
#error-msg{
  font-size: small;
}

